import React, { useState } from "react";
import { AmmData, AmmInData, CoinInfo, IBData } from '@loopring-web/component-lib/src/static-resource';
import { AmmPanelType } from '@loopring-web/component-lib';
import { useTokenMap } from '../../../stores/token';
import { useAmmMap } from '../../../stores/Amm/AmmMap';
import { accountStaticCallBack, ammPairInit, bntLabel, btnClickArray, fnType, makeCache } from '../../../hooks/help';
import { WalletMap } from '@loopring-web/component-lib/static-resource';
import { AmmPoolSnapshot, TickerData, ammPoolCalc, makeJoinAmmPoolRequest, makeExitAmmPoolRequest, TokenInfo, TokenVolumeV3, } from 'loopring-sdk';
import { useCustomDCEffect } from '../../../hooks/common/useCustomDCEffect';
import { useAccount } from '../../../stores/account/hook';
import store from "stores";

import * as sdk from 'loopring-sdk'

export const useAmmPanel = <C extends { [ key: string ]: any }>({
                                                                    pair,
                                                                    walletMap,
                                                                    ammType,
                                                                    snapShotData,
                                                                }
                                                                    : {
    pair: { coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined },
    snapShotData: { tickerData: TickerData | undefined, ammPoolsBalance: AmmPoolSnapshot | undefined } | undefined
    walletMap: WalletMap<C>
    ammType: keyof typeof AmmPanelType
}) => {
    // const walletLayer2State = useWalletLayer2();
    const {coinMap, tokenMap} = useTokenMap();
    const {ammMap} = useAmmMap();
    const {account} = useAccount();

    const [ammCalcData, setAmmCalcData] = React.useState<AmmInData<C> | undefined>();
    const [ammDepositData, setAmmDepositData] = React.useState<AmmData<IBData<C>, C>>({
        coinA: {belong: undefined} as unknown as IBData<C>,
        coinB: {belong: undefined} as unknown as IBData<C>,
        slippage: .5
    } as AmmData<IBData<C>, C>);
    const [ammWithdrawData, setAmmWithdrawData] = React.useState({
        coinA: {belong: undefined} as unknown as IBData<C>,
        coinB: {belong: undefined} as unknown as IBData<C>,
        slippage: .5
    } as AmmData<IBData<C>, C>);
    const [ammDepositBtnI18nKey, setAmmDepositBtnI18nKey] = React.useState<string | undefined>(undefined);
    const [ammWithdrawBtnI18nKey, setAmmWithdrawBtnI18nKey] = React.useState<string | undefined>(undefined);

    const initAmmData = React.useCallback(async (pair: any) => {
        let _ammCalcData: Partial<AmmInData<C>> = ammPairInit(
            {
                pair,
                ammType,
                _ammCalcData: {},
                tokenMap,
                coinMap,
                walletMap: walletMap, //walletLayer2State.walletLayer2,
                ammMap,
                tickerData: snapShotData?.tickerData,
                ammPoolsBalance: snapShotData?.ammPoolsBalance
            })
        setAmmCalcData({...ammCalcData, ..._ammCalcData} as AmmInData<C>);
        if (_ammCalcData.myCoinA) {
            if (ammType === AmmPanelType.Deposit) {
                setAmmDepositData({
                    coinA: _ammCalcData.myCoinA as IBData<C>,
                    coinB: _ammCalcData.myCoinB as IBData<C>,
                    slippage: 0.5
                })
            } else {
                setAmmWithdrawData({
                    coinA: _ammCalcData.myCoinA as IBData<C>,
                    coinB: _ammCalcData.myCoinB as IBData<C>,
                    slippage: 0.5
                })
            }
        }
    }, [snapShotData, walletMap])

    const [snapShot, setSnapShot] = useState<AmmPoolSnapshot>()

    const handleDepositEvent = React.useCallback((data: AmmData<IBData<C>>, type: 'coinA' | 'coinB') => {
        
        // snapShotData is undefined?
        console.log('handleDepositEvent', data, type, snapShotData);
            
        setAmmDepositData({
            coinA: data.coinA,
            coinB: data.coinA,
            slippage: 0.6
        })

        const isAtoB = type === 'coinA'

        const { tokenMap, } = store.getState().tokenMap

        if (!tokenMap || !snapShotData?.ammPoolsBalance) {
            return
        }

        const coinA = tokenMap[data.coinA.belong as string]
        const coinB = tokenMap[data.coinB.belong as string]

        const coinA_TV = snapShotData.ammPoolsBalance.pooled[0]
        const coinB_TV = snapShotData.ammPoolsBalance.pooled[1]

        const covertVal = data.coinA.tradeValue ? sdk.toBig(data.coinA.tradeValue).times('1e' + isAtoB ? coinA.decimals : coinB.decimals).toFixed(0, 0) : '0'
        const { output, ratio } = sdk.ammPoolCalc(covertVal, isAtoB, coinA_TV, coinB_TV)

        console.log('---abc:', output, ratio.toNumber())

        if (isAtoB) {
            // rewrite B
            
                setAmmWithdrawData({
                    coinA: data.coinA,
                    coinB: data.coinA,
                    slippage: 0.6
                })
        } else {
            // rewrite A
        }

    }, []);

    const handleWithDrawEvent = React.useCallback((data: AmmData<IBData<C>>, type: 'coinA' | 'coinB') => {
        console.log('handleWithDrawEvent', data, type);
    }, []);


    useCustomDCEffect(() => {
        const label: string | undefined = accountStaticCallBack(bntLabel)
        setAmmDepositBtnI18nKey(label);
        setAmmWithdrawBtnI18nKey(label)
    }, [account.status, bntLabel])


    const onAmmWithdrawClick = React.useCallback((props: AmmData<IBData<C>>) => {
        const onAmmWithdrawClickArray: typeof btnClickArray = Object.assign(btnClickArray, {
            [ fnType.ACTIVATED ]: [function (props: AmmData<IBData<C>>) {
                //TODO: onWithdraw
                console.log('onAmmWithdraw', props)

                if (props.__cache__) {
                    makeCache(props.__cache__)
                }
            }, props]
        })
        accountStaticCallBack(onAmmWithdrawClickArray)
    }, []);

    const onAmmDepositClick = React.useCallback((props: AmmData<IBData<C>>) => {
        const onAmmDepositClickArray: typeof btnClickArray = Object.assign(btnClickArray, {
            [ fnType.ACTIVATED ]: [function () {
                //TODO: onDeposit
                console.log(' onAmmDeposit', props)
                if (props.__cache__) {
                    makeCache(props.__cache__)
                }
            }, props]
        })
        accountStaticCallBack(onAmmDepositClickArray)
    }, []);

    React.useEffect(() => {
        if (snapShotData) {
            initAmmData(pair)
        }
    }, [snapShotData, pair, walletMap]);


    return {
        ammCalcData,
        ammDepositData,
        ammWithdrawData,
        handleDepositEvent,
        handleWithDrawEvent,
        onAmmWithdrawClick,
        onAmmDepositClick,
        ammDepositBtnI18nKey,
        ammWithdrawBtnI18nKey,
    }
}