import { useDispatch, useSelector } from 'react-redux'
import { StateBase } from '../interface';
import { socketSlice } from './reducer';

export function useSocket(): StateBase & {
    // socketStart:()=>void,
    statusUnset:()=>void,
    sendNewMessage:(socket: { [ key: string]:string[] })=>void,
    socketEnd:()=>void
}
{
    const socket:StateBase & {socket: { [ key: string]:string[] }} = useSelector((state: any) => state.socket)
    const dispatch = useDispatch();
    // const socketStart = () => {
    //     dispatch(socketSlice.actions.socketStart(undefined))
    // }
    const sendNewMessage = (socket: { [ key: string]:string[] }) => {
        dispatch(socketSlice.actions.sendNewMessage(socket))
    }
    const socketEnd = () => {
        dispatch(socketSlice.actions.socketEnd(undefined))
    }
    const statusUnset = ()=>{
        dispatch(socketSlice.actions.statusUnset(undefined))
    }
    return {
        ...socket,
        statusUnset,
        // socketStart,
        sendNewMessage,
        socketEnd,
    }

}
