import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import { STATUS } from '../constant';
import { StateBase } from '../interface';

const initialState: StateBase & {socket: { [ key: string]:string[] }} = {
  socket: {},
  status: 'UNSET',
  errorMessage: null,
}
const socketSlice: Slice<StateBase & {socket: { [ key: string]:string[] }}> = createSlice({
  name: 'socket',
  initialState,
  reducers: {
    socketEnd(state,action:PayloadAction<undefined>){
      state.status = STATUS.PENDING
    },
    sendNewMessage(state, action: PayloadAction<{socket: { [ key: string]:string[] }}>) {
      state.socket = action.payload.socket
    },
    getSocketStatus(state, action: PayloadAction<{socket: { [ key: string]:string[] }}>) {

      // @ts-ignore
      if (action.error) {
        state.status = STATUS.ERROR
        // @ts-ignore
        state.errorMessage = action.error
      }
      // state.socket = action.payload.socket
      state.status = STATUS.DONE
    },

    statusUnset: state => {
      state.status = STATUS.UNSET
    }

  },
});
export { socketSlice };
export const { getSocketStatus, sendNewMessage, socketEnd, statusUnset} = socketSlice.actions;
