import React from 'react';
import { WithTranslation, withTranslation } from 'react-i18next';
import { AmmRecordTable, TradeBtnStatus, TradeTable, TradeTitle, useSettings } from '@loopring-web/component-lib';
import { Currency, getThousandFormattedNumbers, PriceTag, EmptyValueTag } from '@loopring-web/component-lib/static-resource';
import { Box, Breadcrumbs, Grid, Hidden, Link, Typography} from '@material-ui/core';
import { AmmPanelView } from '../AmmPanel';
import moment from 'moment';
import styled from '@emotion/styled/';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import { useCoinPair } from './hooks';
import { ActivityMap } from '../../../stores/Amm/AmmActivityMap';
import { AmmPoolActivityRule, LoopringMap } from 'loopring-sdk/dist/defs/loopring_defs';


//******************** page code ************************//
const BoxStyled = styled(Box)`
  ${({theme}) => theme.border.defaultFrame({c_key: 'blur'})};
  background-color: ${({theme}) => theme.colorBase.background().default};
`;
const StylePaper = styled(Box)`
  width: 100%;
  height: calc(100% - 64px);
  
  //height: 100%;
  flex: 1;
  background-color: ${({theme}) => theme.colorBase.background().default};
  border-radius: ${({theme}) => theme.unit}px;
  padding: ${({theme}) => theme.unit}px 0 ;
  .rdg {
    
    flex: 1;
  }

` as typeof Box;

const applyProps = (index: number) => {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `tabpanel-${index}`,
    }
}

export const CoinPairPanel = withTranslation('common')(<R extends {[key:string]:any},I extends {[key:string]:any}>
({t,  ammActivityMap, ...rest}:
     WithTranslation & { ammActivityMap: LoopringMap<LoopringMap<AmmPoolActivityRule[]>>|undefined } & any) => {    //ActivityMap<I, I>
    //TODO: checkRouter

    const {currency} = useSettings();
    const {tradeFloat,snapShotData, pair, coinPairInfo, walletMap,ammMarketArray,myAmmMarketArray,myAmm} = useCoinPair({ammActivityMap});
    const [tabIndex, setTabIndex] = React.useState<0 | 1>(0);
    const [page, setPage] = React.useState(rest?.page ? rest.page : 1);
    const handleChange = (event: any, newValue: 0 | 1) => {
        setTabIndex(newValue);
        setPage(1);
    }
    const _handlePageChange = React.useCallback((page: number) => {
        setPage(page);
    }, [])


    return <>

        <Grid container>
            <Grid item xs={8}>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link color="textSecondary" href="/#/liquidity/pools">
                        {t('labelAmmList')}
                    </Link>
                    <Typography color={'textSecondary'} display={'flex'} alignItems={'center'}
                                justifyContent={'center'}
                    >{pair.coinAInfo?.simpleName}-{pair.coinBInfo?.simpleName}</Typography>
                </Breadcrumbs>
            </Grid>
            <Grid item xs={4} alignItems={'center'} justifyContent={'flex-end'} display={'flex'}>
                <Link href="/#/liquidity/pools" variant={'h5'}>
                    {t('labelBack')}
                </Link>
            </Grid>
        </Grid>
        <Grid container marginTop={3}>
            <Grid item xs={7}>
                <TradeTitle {...{
                    ...rest, t,
                    ...pair,
                    tradeFloat,
                    isNew: false
                }}></TradeTitle>
            </Grid>
        </Grid>
        <Box flex={1} display={'flex'} alignItems={'stretch'} flexDirection="row" marginTop={3}>
            <Box flex={1} display={'flex'}>
                <Grid container spacing={2} wrap={'nowrap'}>
                    <Grid item md={5} xs={12} paddingRight={2} display={'flex'} flexDirection={'column'}
                          alignItems={'stretch'} justifyContent={'flex-start'}>
                        <BoxStyled paddingY={3} marginBottom={2} paddingX={1 / 2 * 5} display={'flex'} flexDirection={'column'}>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'}>
                                <Typography component={'span'}
                                            style={{textTransform: 'capitalize'}}>{t('labelLiquidity')}</Typography>
                                <Typography component={'span'}
                                            style={{textTransform: 'uppercase'}}>{t('labelAPY')}</Typography>
                            </Typography>
                            <Typography component={'p'} color={'textPrimary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={1}>
                                <>
                                    <Typography
                                        component={'span'}> {typeof coinPairInfo.amountDollar === 'undefined' ? EmptyValueTag :
                                        currency === Currency.dollar ? PriceTag.Dollar + getThousandFormattedNumbers(coinPairInfo.amountDollar) : PriceTag.Yuan + getThousandFormattedNumbers(coinPairInfo.amountYuan ? coinPairInfo.amountYuan : 0)}
                                    </Typography>
                                    <Typography
                                        component={'span'}> {coinPairInfo.APY ? coinPairInfo.APY : EmptyValueTag}%
                                    </Typography>
                                </>


                            </Typography>

                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={2}>
                                <Typography component={'span'}
                                            color={'textSecondary'}>
                                    <Hidden mdDown>{t('labelLPTotal')}</Hidden>{t('labelLPTokens')}</Typography>
                                <Typography component={'span'}
                                            color={'textSecondary'}>{coinPairInfo.totalLPToken}</Typography>
                            </Typography>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={1}>

                                <Typography component={'span'}
                                            color={'textSecondary'}>
                                    <Hidden
                                        mdDown>{t('labelLPTotal')}</Hidden>{coinPairInfo.myCoinA?.simpleName}
                                </Typography>

                                <Typography component={'span'}
                                            color={'textSecondary'}>{coinPairInfo.totalA}</Typography>
                            </Typography>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={1}>
                                <Typography component={'span'}
                                            color={'textSecondary'}>
                                    <Hidden
                                        mdDown>{t('labelLPTotal')}</Hidden>{coinPairInfo.myCoinB?.simpleName}
                                </Typography>
                                <Typography component={'span'}
                                            color={'textSecondary'}>{coinPairInfo.totalB}</Typography>
                            </Typography>

                        </BoxStyled>
                        <BoxStyled paddingY={3} marginBottom={2} paddingX={1 / 2 * 5} display={'flex'} flexDirection={'column'}>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} alignItems={'center'}>
                                <Typography component={'span'} color={'textSecondary'} variant={'body2'} style={{textTransform: 'capitalize'}}>
                                    {coinPairInfo.tradeFloat?.timeUnit} {t('labelVolume')}
                                </Typography>
                                <Typography component={'span'} color={'textSecondary'}>
                                    {t('labelFee')} {/* ' : '*/}
                                </Typography>
                            </Typography>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={1} alignItems={'center'}>
                                <Typography component={'span'} variant={'h4'} color={'textPrimary'}>
                                    {currency === Currency.dollar ? PriceTag.Dollar + getThousandFormattedNumbers(coinPairInfo.tradeFloat && coinPairInfo.tradeFloat.priceDollar ? coinPairInfo.tradeFloat.priceDollar as number : 0)
                                        : PriceTag.Yuan + getThousandFormattedNumbers(coinPairInfo.tradeFloat && coinPairInfo.tradeFloat.priceYuan ? coinPairInfo.tradeFloat.priceYuan as number : 0)}
                                </Typography>
                                <Typography noWrap textAlign={'center'} component={'span'} color={'textSecondary'}
                                            variant={'body2'} textOverflow={'ellipsis'} display={'flex'}
                                            alignItems={'center'}>
                                    <Typography component={'span'} color={'inherit'} variant={'inherit'}
                                                display={'flex'} flexDirection={'column'}>
                                        <Typography component={'span'} color={'inherit'} variant={'inherit'}>
                                            {coinPairInfo.myCoinA?.simpleName}
                                        </Typography>
                                        <Typography component={'span'} color={'inherit'} variant={'inherit'}>
                                            {coinPairInfo.feeA?getThousandFormattedNumbers(coinPairInfo.feeA):EmptyValueTag}
                                        </Typography>
                                    </Typography>
                                    <Typography component={'span'} color={'inherit'} variant={'h5'}
                                                paddingX={1}>{' + '}</Typography>
                                    <Typography component={'span'} color={'inherit'} variant={'inherit'}
                                                display={'flex'} flexDirection={'column'}>
                                        <Typography component={'span'} color={'inherit'} variant={'inherit'}>
                                            {coinPairInfo.myCoinB?.simpleName}
                                        </Typography>
                                        <Typography component={'span'} color={'inherit'} variant={'inherit'}>
                                            {coinPairInfo.feeB?getThousandFormattedNumbers(coinPairInfo.feeB):EmptyValueTag}
                                        </Typography>
                                    </Typography>
                                </Typography>
                            </Typography>
                        </BoxStyled>
                        <BoxStyled paddingY={3} marginBottom={2} paddingX={1 / 2 * 5} display={'flex'} flexDirection={'column'}>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'}>
                                <Typography component={'span'}
                                            color={'textSecondary'}> {t('labelMyPoolShare')}</Typography>
                            </Typography>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={1}>
                                <Typography component={'span'}
                                            color={'textSecondary'}>
                                    <>{myAmm.balanceA ? getThousandFormattedNumbers(myAmm.balanceA,2)  : EmptyValueTag}</>
                                    <> { coinPairInfo.myCoinA?.simpleName}</>
                                </Typography>
                                <Typography component={'span'} color={'textecondary'}
                                            variant={'h5'}> {myAmm.feeA?getThousandFormattedNumbers(myAmm.feeA):EmptyValueTag} {coinPairInfo.myCoinA?.simpleName}  </Typography>
                            </Typography>
                            <Typography component={'p'} color={'textSecondary'} display={'flex'}
                                        justifyContent={'space-between'} marginTop={1}>
                                <Typography component={'span'}
                                            color={'textSecondary'}>
                                    <>{myAmm.balanceB ? getThousandFormattedNumbers(myAmm.balanceB,2) : EmptyValueTag}</>
                                    <> {coinPairInfo.myCoinB?.simpleName}</>
                                </Typography>
                                <Typography component={'span'} color={'textSecondary'}
                                            variant={'h5'}> {myAmm.feeB?getThousandFormattedNumbers(myAmm.feeB):EmptyValueTag} {coinPairInfo.myCoinB?.simpleName}</Typography>
                            </Typography>
                        </BoxStyled>
                        <BoxStyled paddingY={3} paddingX={1 / 2 * 5} display={'flex'}
                                   flexDirection={'column'}>
                            <Box display={'flex'} flexDirection={'row'} justifyContent={'space-between'}>
                                <Typography display={'flex'} flexDirection={'column'} component={'div'}>
                                    <Typography variant={'body2'} component={'h5'} color={'textSecondary'}>
                                        {t('labelReward')}
                                    </Typography>
                                    <Typography variant={'body1'} component={'span'} color={'textPrimary'}>
                                        {typeof coinPairInfo.isActivity === 'undefined' ? EmptyValueTag : <>
                                            <Typography
                                                component={'span'}>{coinPairInfo.activity?getThousandFormattedNumbers(coinPairInfo.activity.totalRewards):EmptyValueTag} </Typography>
                                            <Typography
                                                component={'span'}>{coinPairInfo.activity?.rewardToken?.simpleName}</Typography>
                                        </>}
                                    </Typography>
                                </Typography>
                                <Typography display={'flex'} flexDirection={'column'} alignItems={'flex-end'}
                                            component={'div'}>
                                    <Typography variant={'body2'} component={'h5'} color={'textSecondary'}>
                                        {t('labelMyReward')}
                                    </Typography>
                                    <Typography variant={'body1'} component={'span'} color={'textPrimary'}>
                                        {/*{typeof coinPairInfo.isActivity === 'undefined' ? EmptyValueTag : <>*/}
                                        {typeof myAmm.reward === 'undefined' ? EmptyValueTag : <>

                                        <Typography component={'span'}> {getThousandFormattedNumbers(myAmm.reward)} </Typography>
                                            <Typography
                                                component={'span'}> {coinPairInfo.activity?.rewardToken?.simpleName}</Typography></>}

                                    </Typography>
                                </Typography>
                            </Box>
                            <Typography alignSelf={'flex-start'} variant={'body2'} color={'textSecondary'}
                                        component="span" marginTop={1}>
                                {typeof coinPairInfo.isActivity === 'undefined' ? t('labelNoActiveEvent')
                                    : <>
                                        {t('labelDate')} :
                                       <> {moment(coinPairInfo.activity?.duration.from).format('L') + ' - ' + moment(coinPairInfo.activity?.duration.to).format('L')}</>
                                    </>
                                }
                            </Typography>
                        </BoxStyled>
                    </Grid>
                    <Hidden mdDown>
                        <Grid item md={7} paddingRight={2}>


                        </Grid>
                    </Hidden>
                </Grid>
            </Box>
            <Box display={'flex'}>
                <AmmPanelView pair={pair} walletMap={walletMap} snapShotData={snapShotData}/>
            </Box>
        </Box>
        <Grid container marginY={3}>
            <Grid item xs={12}>
                <Tabs value={tabIndex}
                      onChange={handleChange}
                      aria-label="tabs switch">
                    <Tab label={t('labelAll')} {...applyProps(0)} />
                    <Tab label={t('labelMe')} {...applyProps(1)} />
                </Tabs>
                <StylePaper marginTop={2} >
                    {/*ammRecordArray*/}
                    {tabIndex === 0 ?  <AmmRecordTable
                        rawData={ammMarketArray}
                        handlePageChange={_handlePageChange} page={page}
                    /> : <AmmRecordTable rawData={myAmmMarketArray}  handlePageChange={_handlePageChange} page={page} /> }
                </StylePaper>
            </Grid>

        </Grid>

    </>
})



