import store from '../../stores';
import { AccountStatus } from '../../state_machine/account_machine_spec';
import { setShowAccountInfo, setShowConnect, setShowDeposit } from '@loopring-web/component-lib';
import React from 'react';

export enum fnType {
    UNKNOWN,
    UNCONNNECTED,
    CONNECTED,
    NOACCOUNT,
    DEPOSITING,
    DEPOSIT_TO_CONFIREM,
    UNACTIVATED,
    ARPROVING,
    APPROV_TO_CONFIRM,
    LOCKED,
    ACTIVATED,
    DEFAULT
}
export const accountStaticCallBack = (props: { [key: number ]:[ fn: (props: any) => any, args?: object] }) => {
    const {status} = store.getState().account
    let fn, args;
    switch (status) {
        case AccountStatus.UNKNOWN:
            [fn, args] = props[ fnType.UNKNOWN ]?props[ fnType.UNKNOWN ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            return
        case AccountStatus.UNCONNNECTED:
            [fn, args] = props[ fnType.UNCONNNECTED ]?props[ fnType.UNCONNNECTED ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.CONNECTED:
            [fn, args] = props[ fnType.CONNECTED ]?props[ fnType.CONNECTED ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.NOACCOUNT:
            [fn, args] = props[ fnType.NOACCOUNT ]?props[ fnType.NOACCOUNT ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.DEPOSITING:
            [fn, args] = props[ fnType.DEPOSITING ]?props[ fnType.DEPOSITING ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.DEPOSIT_TO_CONFIREM:
            [fn, args] = props[ fnType.DEPOSIT_TO_CONFIREM ]?props[ fnType.DEPOSIT_TO_CONFIREM ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.UNACTIVATED:
            [fn, args] = props[ fnType.UNACTIVATED ]?props[ fnType.UNACTIVATED ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.ARPROVING:
            [fn, args] = props[ fnType.ARPROVING ]?props[ fnType.ARPROVING ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.APPROV_TO_CONFIRM:
            [fn, args] = props[ fnType.APPROV_TO_CONFIRM ]?props[ fnType.APPROV_TO_CONFIRM ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.LOCKED:
            [fn, args] = props[ fnType.LOCKED ]?props[ fnType.LOCKED ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        case AccountStatus.ACTIVATED:
            [fn, args] = props[ fnType.ACTIVATED ]?props[ fnType.ACTIVATED ]:[];
            if (typeof fn === 'function') {
                return fn.call(this, args);
            }
            break;
        default:
            break;

    }
    [fn, args] = props[ fnType.DEFAULT ]?props[ fnType.DEFAULT ]:[];
    if (typeof fn === 'function') {
        return fn.call(this, args);
    }
}


export const bntLabel: { [ key: number ]: [fn: (props: any) => any, args?: object] } = {
    [ fnType.UNCONNNECTED ]: [
        function () {
            return `labelConnectWallet`
        }
    ]
    , [ fnType.DEFAULT ]: [
        function () {
            return `depositTitleAndActive`
        }
    ]
    , [ fnType.ACTIVATED ]: [
        function () {
            return undefined
        }
    ]
    , [ fnType.LOCKED ]: [
        function () {
            return `labelUnLock`
        }
    ]
};


export const btnClickArray: { [ key: number ]: [fn: (props: any) => any, args?: object] } = {
    [ fnType.UNCONNNECTED ]: [
        function () {
            // setShowConnect({isShow: true})
            store.dispatch(setShowConnect({isShow: true}))
        }
    ]
    , [ fnType.DEFAULT ]: [
        function () {
            store.dispatch(setShowDeposit({isShow: true}))
            // ShowDeposit(true)
        }
    ]

    , [ fnType.LOCKED ]: [
        function () {
            store.dispatch(setShowAccountInfo({isShow: true}))
        }
    ]
};


